      </a-list>

      <a-list v-show="radioValue === 'scripts'" :grid="{ gutter: 5, xs: 1, sm: 2, md: 4, lg: 4, xl: 4, xxl: 4 }" :data-source="data1" style="margin-left: -8px; margin-right: -8px; margin-top: 20px;">
        <template #renderItem="{ item }">
