        </a-form>

        <div class="list_bottom_box">
          <p style="font-size: 1.125rem;color: rgba(153, 153, 153, 1); text-align: center">Floor price <span style="color: #5AE86F">{{'0.0155 AVAX'}}</span>, Your price $ 0<span style="color: rgba(204, 204, 204, 1)">(Per Mint)</span></p>

