<script>

import {defineComponent, reactive, ref, toRefs,onMounted,watch} from "vue";
import {getAddressScripts,getAddressTokens,stringToHex} from '@/utils/api'
import {useStore} from "vuex"

export default defineComponent({
  name: "Owner",
  setup() {
    const transferRef = ref();
    const listRef = ref();
    const store = useStore();
    let dayjs = require('dayjs')
    watch(() => store.state.wallet_address, (val, old) => {
       Methods.loadData();
    })
    const Data = reactive({
      radioValue: 'tokens',
      data: [],
      data1: [],
      page:1,
      title:"",
      itemData: {},
      transferOpen: false,
      transferState: {
        amount: '',
        to: '',
      },
      transferLoading: false,
      listOpen: false,
      listState: {
        price: '',
        amount: '',
      },
      selectValue: '1',
      listLoading: false,
    });
    onMounted(() => {
      Methods.loadData();
    });
    const Methods = {
      loadData(){
        Data.page=1
        Data.tableLoading=true
        if(Data.radioValue=="tokens"){
          getAddressTokens(store.state.wallet_address).then((response) => {
            Data.tableLoading=false
            if(response.code==0){
              Data.data = response.data
            }else{
              Data.tableLoading=false
              notification["error"]({message:"Error",description:response.msg});
            }
          })
        }else{
          getAddressScripts(store.state.wallet_address).then((response) => {
            Data.tableLoading=false
            if(response.code==0){
              Data.data1 = response.data
            }else{
              Data.tableLoading=false
              notification["error"]({message:"Error",description:response.msg});
            }
          })
        }
      },
    
      onRefresh() {
        Methods.loadData()
      },
      showTransferModal(item) {
        Data.title="Transfer "+item.name+""
        Data.itemData = item;
        Data.transferOpen = true;
      },
      max(){
        Data.transferState.amount = ""+Data.itemData.quantity
      },
      handleTransferCancel() {
        Data.transferOpen = false;
        transferRef.value.resetFields();
      },

      onTransferSubmit() {
        transferRef.value.validate().then(() => {
          var reg = /^[\d]+$/;
          if(!reg.test(Data.transferState.amount.trim())){
             window.notification["error"]({message:"Error",description:'Amount only numbers can be entered'});
             return
          }
          if(!store.state.web3){
            store.dispatch("connectWallet")
            window.notification["error"]({message:"Error",description:"Connect your wallet"});
            return;
          }
          if(!store.state.web3.utils.isAddress(Data.transferState.to.trim())){
            window.notification["error"]({message:"Error",description:"Input address error"});
            return;
          }
          Data.transferLoading = true;
          let json = stringToHex('{"p":"script","op":"transfer","tick":"'+Data.itemData.name.toUpperCase().trim()+'","amt":"'+Data.transferState.amount.trim()+'","to":"'+Data.transferState.to.trim()+'"}');
          window.ethereum.request({
              method: 'eth_sendTransaction',
              params: [{from:store.state.wallet_address,to:store.state.toAddress,data:json}],
            }).then((txHash) => {
              window.notification["success"]({message:"",description:"Transfer success"});
              Data.transferOpen = false
              Data.transferLoading = true;
              transferRef.value.resetFields();
            }).catch((error) =>  {
              Data.transferLoading = false
            });
        }).catch(error => {
           Data.transferLoading = false
           window.notification["error"]({message:"Error",description:error});
        });
      },

      showListModal(item) {
        //Data.itemData = item;
        //Data.listOpen = true;
        window.notification["warning"]({message:"",description:"Coming Soon"});
      },

      handleChange(value) {
        console.log(`selected ${value}`);
      },

      handleListCancel() {
        Data.listOpen = false;
        listRef.value.resetFields();
      },

      onListSubmit() {
        listRef.value.validate().then(() => {
          Data.listLoading = true;
          console.log('list', JSON.stringify(Data.listState));
          setTimeout(() => {
            Data.listLoading = false;
            Data.listOpen = false;
          }, 2000);
        }).catch(error => {
          console.log('error', error);
        });
      }
    }

    return {
      transferRef,
      listRef,
      ...toRefs(Data),
      ...Methods,
      dayjs
    }
  }
})
</script>

